import {
  Component,
  Element,
  Event,
  EventEmitter,
  h,
  Host,
  Listen,
  Prop,
  State,
} from '@stencil/core';
import { createColorClasses } from '../../../utils/functions/color.function';
import { TpColor } from '../../../utils/types/color.type';
import { TP_CONTEXT_MENU } from './utils/context-menu.constant';

@Component({
  tag: 'tp-context-menu',
  styleUrl: 'context-menu.scss',
  assetsDirs: ['assets'],
  scoped: true,
})
export class TpContextMenu {
  /**
   * Define a variação de cor do componente.
   */
  @Prop({ reflect: true }) color: TpColor;

  /**
   * Define o estato inicial do componente.
   */
  @Prop({ reflect: true }) initialCollapsedState = true;

  /**
   * Define a variação de estado do componente.
   */
  @State() collapsed = true;

  /**
   * Emitido quando a propriedade collapsed é alterada.
   */
  @Event() tpCollapse: EventEmitter<boolean>;

  /**
   * Referência ao host.
   */
  @Element() host: HTMLElement;

  /**
   * Métodos internos.
   */
  toggle() {
    this.collapsed = !this.collapsed;
    this.tpCollapse.emit(this.collapsed);
  }

  /**
   * Lifecycle hooks.
   */
  componentWillLoad() {
    if (this.initialCollapsedState !== this.collapsed) {
      this.collapsed = this.initialCollapsedState;
    }
  }

  /**
   * Event listeners.
   */
  @Listen('click', { target: 'body' })
  closeOnOutsideClick(event: MouseEvent) {
    const target = event.target as Node;

    if (!this.collapsed && !this.host.contains(target)) {
      this.collapsed = true;
    }
  }

  @Listen('keydown')
  closeOnEscapeKey(ev: KeyboardEvent) {
    if (!this.collapsed && ev.key === 'Escape') {
      this.collapsed = true;
    }
  }

  render() {
    const { color, collapsed } = this;

    return (
      <Host
        class={createColorClasses(color, {
          'tp-context-menu': true,
          'tp-context-menu--collapsed': collapsed,
        })}
      >
        <ion-button
          icon-only
          onClick={() => this.toggle()}
          class="tp-context-menu__button"
          fill="clear"
          // @ts-ignore
          size="sm"
        >
          <ion-icon
            slot="icon-only"
            class="tp-context-menu__icon"
            src={TP_CONTEXT_MENU.icons.moreVertical}
          ></ion-icon>
        </ion-button>

        <div class="tp-context-menu__content">
          <div class="tp-context-menu__top">
            <ion-button
              icon-only
              onClick={() => this.toggle()}
              class="tp-context-menu__inner-button"
              fill="clear"
              // @ts-ignore
              size="sm"
            >
              <ion-icon
                slot="icon-only"
                class="tp-context-menu__inner-icon"
                src={TP_CONTEXT_MENU.icons.moreVertical}
              ></ion-icon>
            </ion-button>
            <div>
              <slot name="item"></slot>
            </div>
          </div>
          <slot name="info"></slot>
        </div>
      </Host>
    );
  }
}
